<script>
import {CDPContextReadOnlyStore as CDPContext} from "../stores/CDPContextReadOnlyStore";
import {CDPStateStore as CDPState} from "../stores/CDPStateStore";
import AnswerOption from "./AnswerOption.svelte";
import ImageItem from "./ImageItem.svelte";
import {onDestroy, onMount} from "svelte";
import {v4 as uuidv4} from "uuid";

const BOTTOM_SAFETY_MARGIN_PX = 20;
let isLocked = false;
let isShowFeedback = false;
let buttonElemRow;
let isSubmitted = false;
let allowMultiSelect = false;
let submitButton;
let uuid;
$: selectedOptValues = allowMultiSelect ? [] : "";

$: if (allowMultiSelect !== $CDPState.allowMultiSelect) {
    if (submitButton) {
        submitButton.innerText = "SUBMIT";
    }
    isLocked = false;
    allowMultiSelect = $CDPState.allowMultiSelect;
}

onMount(() => {
    window.addEventListener("resize", resizeContainerToMySize);
    uuid = uuidv4();
    resizeContainerToMySize();
});

onDestroy(() => {
    window.removeEventListener("resize", resizeContainerToMySize);
});

function handleOptSelected(val, checked) {
    submitButton.innerText = "SUBMIT";
    isShowFeedback = false;

    if (allowMultiSelect) {
        selectedOptValues = isSubmitted ? [] : [...selectedOptValues];
        selectedOptValues = checked ? [...selectedOptValues, val] : selectedOptValues.filter(v => v !== val);
    } else {
        selectedOptValues = val;
    }

    isSubmitted = false;
}

function handleOptSubmitted(event) {
    event.target.innerText = "TRY AGAIN";
    isShowFeedback = !isShowFeedback;
    isSubmitted = true;
}

function correctAnswersSelected(correct, selected) {
    if (correct.length > 0) {
        return correct.every(v => selected.includes(v));
    }
}

// This is a temporary fix until SPF supports auto resizing of its containers
function resizeContainerToMySize() {
    if ($CDPContext !== "LEARNER") return;

    // Hide the scrollbar so it doesn't show when expanding/collapsing the answers.
    window.document.body.style.overflowY = "hidden";

    if (!buttonElemRow) {
        console.error("button element missing");
        return;
    }
    const desiredHeightPx = buttonElemRow.getBoundingClientRect().bottom + BOTTOM_SAFETY_MARGIN_PX;
    const spfContainer = window.parent.document.getElementById(window?.frameElement?.id)?.parentElement.parentElement;
    if (!spfContainer) {
        console.error("failed to find mcq container");
        return;
    }
    spfContainer.style.height = `${desiredHeightPx}px`;
}

$: {
    let selectedItems = $CDPState.choices.filter(c => selectedOptValues.includes(c.choiceLabel));
    let correctItems = $CDPState.choices.filter(c => c?.correct);
    if (correctAnswersSelected(correctItems, selectedItems) && isShowFeedback) {
        isLocked = true;
    }
}

</script>

<style lang="scss">main {
  text-align: left;
  padding: 0;
  max-width: 240px;
  margin: 0 auto;
  color: var(--text-color);
}

@media (min-width: 640px) {
  main {
    max-width: none;
  }
}
div.question-prompt {
  margin: 0 0 30px 0;
  font-family: var(--title-font);
}

.button-row {
  padding-bottom: 20px;
}
.button-row button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: var(--primary-color);
  color: white;
  font-family: Open Sans, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
}
.button-row button:hover {
  background-color: var(--primary-color-40);
}
.button-row button:active {
  background-color: var(--primary-color-70);
}
.button-row button:focus {
  box-shadow: 0 0 0 2px var(--text-color);
}

.component-label {
  font-family: var(--title-font);
}

.component-label-container {
  margin-bottom: 15px;
}</style>

<main role="radiogroup" tabindex="0" aria-labelledby={uuid}>
    <div class="question">
        {#if $CDPState.activityTitle || $CDPState.activityLabel || $CDPState.instructions}
            <div class="component-label-container">
                <h4 class="component-label">
                    {#if $CDPState.activityLabel}
                        {@html $CDPState.activityLabel}
                    {/if}
                    {#if $CDPState.activityTitle}
                        <span class="component-title">{@html $CDPState.activityTitle}</span>
                    {/if}
                </h4>
                {#if $CDPState.instructions}
                    <div class="component-instructions">
                        {@html $CDPState.instructions}
                    </div>
                {/if}
            </div>
        {/if}
        <div id={uuid} class="question-prompt">
            {@html $CDPState.label}
            <ImageItem className="question-image" image={$CDPState.questionImage} />
        </div>
    </div>
    <div class="answer-options">
        {#each $CDPState.choices as opt, i (i)}
            <AnswerOption
                {...opt}
                handleResized={resizeContainerToMySize}
                position={i}
                setChoiceLabel={choiceLabel => (opt.choiceLabel = choiceLabel)}
                isLocked={isLocked}
                selectedOptValues={selectedOptValues}
                isShowFeedback={isShowFeedback}
                handleOptSelected={handleOptSelected}
                allowMultiSelect={allowMultiSelect}
            />
        {/each}
    </div>
    <div class="button-row" bind:this={buttonElemRow} style="text-align:{$CDPState.buttonPosition}">
        {#if !isLocked}<button bind:this={submitButton} tabindex="0" on:click={event => handleOptSubmitted(event)}>SUBMIT</button>{/if}
    </div>
</main>
