<script>
import {iconIncorrect, iconCorrect} from "../assets/icons";
import {slide} from "svelte/transition";
import ImageItem from "./ImageItem.svelte";

export let choiceLabel;
export let choiceImage;
export let correct;
export let selectedOptValues;
export let isLocked;
export let feedback;
export let feedbackImage;
export let handleOptSelected;
export let position;
export let handleResized;
export let isShowFeedback;
export let allowMultiSelect;
export let setChoiceLabel;

const responseIcon = correct ? iconCorrect : iconIncorrect;
const responseHeader = correct ? `<p dir="ltr">That's correct.</p>` : `<p dir="ltr">That's incorrect. Try again.</p>`;
choiceLabel = choiceLabel ? choiceLabel : `<p dir="auto">Text of choice ${++position}</p>`;
setChoiceLabel(choiceLabel);

function handleInputSelected(event) {
    event.target.value = choiceLabel;
    handleOptSelected(choiceLabel, event.target.checked);
}

</script>

<style lang="scss">div.answer {
  margin: 15px 0 30px 0;
  display: grid;
  grid-template-columns: 40px auto;
  color: var(--text-color);
  font-family: var(--body-font);
}

input.selector {
  -webkit-appearance: none;
  border-radius: 50%;
  outline: none;
  box-shadow: 0 0 0 1px var(--text-color);
  margin: 0;
}
input.selector.checkbox {
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}
input.selector.checkbox:before {
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
input.selector.checkbox:not(:checked):not(.locked):hover {
  border-radius: 2px;
}
input.selector.checkbox:checked {
  box-shadow: inset 0 0 0 0 transparent, inset 0 0 0 10px var(--primary-color);
  border-color: transparent;
}
input.selector.checkbox:checked:before {
  width: 9px;
  height: 9px;
  background-color: #fff;
}
input.selector:before {
  content: "";
  display: block;
  width: 60%;
  height: 60%;
  margin: 20% auto;
  border-radius: 50%;
  cursor: pointer;
}
input.selector:checked {
  padding: 2px;
}
input.selector:checked:before {
  width: 100%;
  height: 100%;
  margin: 0;
  background: var(--primary-color);
}
input.selector:not(:checked):not(.locked):hover, input.selector:not(.locked):focus {
  cursor: pointer;
  box-shadow: 0 0 0 2px var(--text-color);
  border-radius: 50%;
}

input.selector,
div.feedback-icon {
  width: 20px;
  height: 20px;
  grid-column: 1;
}

.feedback-icon.correct {
  border-radius: 10px;
  background-color: var(--success-color);
}
.feedback-icon.incorrect {
  border-radius: 10px;
  background-color: var(--error-color);
}

label.option:not(.locked) {
  grid-column: 2;
  cursor: pointer;
}

label.option {
  /* As a complex math expression will change the line height and it causes an issue
  specially for multi lines labels, a fixed line height has been added for the label,
  so to remove the line height of the first line in order to align it with the radio
  button, :before pseudo-element with negative margin has been used.*/
}
label.option :global(p::before) {
  content: "";
  display: block;
  margin: -1em 0;
}

div.response {
  grid-column: 2;
  padding: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
}
div.response.correct {
  background-color: var(--success-color-10);
}
div.response.correct span.response-header {
  color: var(--success-color);
}
div.response.incorrect {
  background-color: var(--error-color-10);
}
div.response.incorrect span.response-header {
  color: var(--error-color);
}

@media (max-width: 640px) {
  div.response {
    grid-column: 1/span 2;
  }
}
.response-header {
  margin-bottom: 15px;
  grid-column: 2;
  font-weight: bold;
  size: 40px;
}

p.feedback {
  margin-top: 8px;
}

label.option :global(p),
p.feedback :global(p) {
  line-height: 3.25;
}
label.option :global(span),
p.feedback :global(span) {
  display: inline-block;
  vertical-align: middle;
  line-height: 1.3;
}</style>

<div class="answer">
    {#if isShowFeedback && selectedOptValues.includes(choiceLabel)}
        <div class={`feedback-icon ${correct ? 'correct' : 'incorrect'}`}>
            {@html responseIcon}
        </div>
    {:else}
        <input
            id={`opt${position}`}
            name="mcq-group"
            class={`selector ${isLocked ? 'locked' : ''}`}
            class:checkbox={allowMultiSelect}
            type={allowMultiSelect ? 'checkbox' : 'radio'}
            on:input={event => handleInputSelected(event)}
            value={choiceLabel}
            disabled={allowMultiSelect ? isLocked : isLocked}
            role={allowMultiSelect ? 'checkbox' : 'radio'}
            tabindex="0"
        />
    {/if}

    <label for={`opt${position}`} class={`option ${isLocked ? 'locked' : ''}`}>
        {@html choiceLabel}
    </label>
    <ImageItem className="choice-image" image={choiceImage} />

    {#if isShowFeedback && selectedOptValues.includes(choiceLabel)}
        <div transition:slide on:introend={handleResized} on:outroend={handleResized} class={`response ${correct ? 'correct' : 'incorrect'}`}>
            <span class="response-header">{@html responseHeader}</span>
            {#if feedback}
                <p class="feedback">
                    {@html feedback}
                </p>
            {/if}
            <ImageItem image={feedbackImage} />
        </div>
    {/if}
</div>
